@import url(https://fonts.googleapis.com/css?family=Roboto);
.navbar-comp {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  left: 0;
  display: flex;
  z-index: 10;
  background: #0f743ccc;
  padding: 20px 10px;
  justify-content: space-between;
  width: 100%; }
  .navbar-comp .main-link {
    font-size: 32px;
    color: #ffffffdc; }
  .navbar-comp .navbar-links {
    font-size: 20px;
    display: flex;
    align-items: center;
    color: #dfdfdfdc; }
    .navbar-comp .navbar-links > a {
      padding: 0 3px; }
    .navbar-comp .navbar-links .active-link {
      color: white; }

.footer-comp {
  display: flex;
  background: #0f743ccc;
  padding: 20px 10px;
  justify-content: space-between;
  color: white;
  width: 100%; }
  .footer-comp .copyright {
    font-size: 16px; }
  .footer-comp .footer-links {
    font-size: 15px;
    display: flex;
    align-items: center;
    color: #dfdfdfdc; }
    .footer-comp .footer-links > a {
      padding: 0 3px; }

html, body {
  height: 100vh;
  min-width: 320px; }

body {
  margin: 0;
  background-color: #3cb372cc;
  font-family: 'Roboto', sans-serif; }

a, a:link, a:visited, a:focus {
  text-decoration: none;
  outline: none;
  color: inherit; }

button, a {
  transition: all 0.3s ease-in-out; }

a:hover {
  color: #ffffff; }

div, h4 {
  color: inherit;
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.app {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-evenly; }

.slime-img {
  max-height: 300px;
  max-width: 400px; }

img {
  max-width: 100%;
  max-height: 100%; }

.slime-heading {
  color: white;
  text-align: center; }

.r {
  color: red; }

.app-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 100%;
  justify-content: space-between; }

.commands-comp {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; }
  @media only screen and (max-width: 868px) {
    .commands-comp {
      flex-direction: column; } }

